import { useMemo } from "react";
import { UserCircle, Building2, MapPin, Landmark } from "lucide-react";
import { TreeNode } from "@/shared/components/navigation/nav-tree"; // Í≥µÌÜµ Ìä∏Î¶¨ ÌÉÄÏûÖ
import { SECTORS, CENTERS, DEPARTMENTS, USERS } from "../data/user-mock";
import { User, Department, Center, Sector } from "../model/types";

// Ïù¥ ÌõÖÏóêÏÑú ÏÇ¨Ïö©Ìï† Ï†ÑÏ≤¥ Ïú†ÎãàÏò® ÌÉÄÏûÖ
export type OrgData = Sector | Center | Department | User;

// ÏòµÏÖò ÌÉÄÏûÖ Ï†ïÏùò
interface UseOrgTreeNodesOptions {
  searchTerm: string;
  includeUsers?: boolean; // ÏÇ¨Ïö©Ïûê Ìè¨Ìï® Ïó¨Î∂Ä (true: ÏÇ¨Ïö©Ïûê Í¥ÄÎ¶¨, false: Î∂ÄÏÑú Í¥ÄÎ¶¨)
}

export function useOrgTreeNodes({
  searchTerm,
  includeUsers = false,
}: UseOrgTreeNodesOptions) {
  return useMemo<TreeNode<OrgData>[]>(() => {
    // [Helper] ÏÇ¨Ïö©Ïûê ÎÖ∏Îìú Îß§Ìïë Ìï®Ïàò
    const mapUsers = (deptId: number): TreeNode<OrgData>[] => {
      if (!includeUsers) return []; // ÏòµÏÖòÏù¥ Í∫ºÏ†∏ÏûàÏúºÎ©¥ Îπà Î∞∞Ïó¥ Î∞òÌôò

      return USERS.filter(
        (u) =>
          u.department_id === deptId &&
          (u.name.includes(searchTerm) || u.login_id.includes(searchTerm)),
      ).map((user) => ({
        id: user.id,
        label: user.name,
        icon: UserCircle,
        subInfo: [user.login_id],
        data: user,
      }));
    };

    // [Main Logic] Î≥∏Î∂Ä -> ÏÑºÌÑ∞ -> Î∂ÄÏÑú -> (ÏÇ¨Ïö©Ïûê) ÏàúÌöå
    return SECTORS.map((sector): TreeNode<OrgData> | null => {
      // 1. ÏßÅÏòÅ Î∂ÄÏÑú Ï≤òÎ¶¨
      const directDeptNodes: TreeNode<OrgData>[] = DEPARTMENTS.filter(
        (d) =>
          d.sector_id === sector.id &&
          !d.center_id &&
          // ÏÇ¨Ïö©Ïûê Î™®ÎìúÍ∞Ä ÏïÑÎãê Îïê Î∂ÄÏÑúÎ™ÖÏúºÎ°ú Í≤ÄÏÉâ, ÏÇ¨Ïö©Ïûê Î™®ÎìúÏùº Îïê Í≤ÄÏÉâ Î°úÏßÅÏù¥ ÏïÑÎûò userNodesÏóê ÏùòÏ°¥
          (!includeUsers
            ? d.name.includes(searchTerm) || d.code?.includes(searchTerm)
            : true),
      )
        .map((dept): TreeNode<OrgData> | null => {
          const userNodes = mapUsers(dept.id);

          // ÏÇ¨Ïö©Ïûê Î™®ÎìúÏù∏Îç∞ Í≤ÄÏÉâÏñ¥Í∞Ä ÏûàÍ≥†, Îß§Ïπ≠Îêú ÏÇ¨Ïö©ÏûêÍ∞Ä ÏóÜÏúºÎ©¥ Î∂ÄÏÑú Ïà®ÍπÄ
          if (includeUsers && searchTerm && userNodes.length === 0) return null;

          // Î∂ÄÏÑú Î™®ÎìúÏù∏Îç∞ Í≤ÄÏÉâÏñ¥Í∞Ä ÏûàÍ≥†, Î∂ÄÏÑúÎ™ÖÏù¥ Îß§Ïπ≠ ÏïàÎêòÎ©¥ Ïà®ÍπÄ (ÏúÑ ÌïÑÌÑ∞ÏóêÏÑú Ïù¥ÎØ∏ Í±∏Îü¨Ïßê)

          return {
            id: `dept-${dept.id}`,
            label: dept.name,
            icon: Building2,
            children: userNodes, // ÏÇ¨Ïö©Ïûê Î™®ÎìúÎ©¥ Ï±ÑÏõåÏßÄÍ≥†, ÏïÑÎãàÎ©¥ Îπà Î∞∞Ïó¥
            data: dept,
          };
        })
        .filter((node): node is TreeNode<OrgData> => node !== null);

      // 2. ÏÑºÌÑ∞ Î∞è ÏÇ∞Ìïò Î∂ÄÏÑú Ï≤òÎ¶¨
      const centerNodes: TreeNode<OrgData>[] = CENTERS.filter(
        (c) => c.sector_id === sector.id,
      )
        .map((center): TreeNode<OrgData> | null => {
          const centerDeptNodes: TreeNode<OrgData>[] = DEPARTMENTS.filter(
            (d) =>
              d.center_id === center.id &&
              (!includeUsers
                ? d.name.includes(searchTerm) || d.code?.includes(searchTerm)
                : true),
          )
            .map((dept): TreeNode<OrgData> | null => {
              const userNodes = mapUsers(dept.id);

              if (includeUsers && searchTerm && userNodes.length === 0)
                return null;

              return {
                id: `dept-${dept.id}`,
                label: dept.name,
                icon: Building2,
                children: userNodes,
                data: dept,
              };
            })
            .filter((node): node is TreeNode<OrgData> => node !== null);

          // ÌïòÏúÑ Î∂ÄÏÑúÍ∞Ä ÏóÜÏúºÎ©¥ ÏÑºÌÑ∞ Ïà®ÍπÄ
          if ((includeUsers || searchTerm) && centerDeptNodes.length === 0)
            return null;

          return {
            id: `center-${center.id}`,
            label: center.name,
            icon: MapPin,
            children: centerDeptNodes,
            data: center,
          };
        })
        .filter((node): node is TreeNode<OrgData> => node !== null);

      // ÌïòÏúÑ ÏöîÏÜåÍ∞Ä ÏóÜÏúºÎ©¥ Î≥∏Î∂Ä Ïà®ÍπÄ
      if (
        (includeUsers || searchTerm) &&
        directDeptNodes.length === 0 &&
        centerNodes.length === 0
      ) {
        return null;
      }

      return {
        id: `sector-${sector.id}`,
        label: sector.name,
        icon: Landmark,
        children: [...directDeptNodes, ...centerNodes],
        data: sector,
      };
    }).filter((node): node is TreeNode<OrgData> => node !== null);
  }, [searchTerm, includeUsers]);
}
