"use client";

import React from "react";
import { ChevronDown, ChevronRight, LucideIcon } from "lucide-react";
import {
  Collapsible,
  CollapsibleContent,
  CollapsibleTrigger,
} from "@/shared/components/ui/collapsible";
import { NavListItem } from "@/shared/components/navigation/nav-list-item";
import { cn } from "@/shared/lib/utils";

// ‚ú® [ÌïµÏã¨ 1] TÏùò Í∏∞Î≥∏Í∞í Ï†úÍ±∞. TÎäî Î∞òÎìúÏãú Í∞ùÏ≤¥(object)Ïó¨Ïïº ÌïúÎã§Í≥† Ï†úÏïΩ.
// Ïù¥Ï†ú TreeNodeÎ•º Ïì∏ ÎïåÎäî Î∞òÎìúÏãú ÌÉÄÏûÖÏùÑ Î™ÖÏãúÌïòÍ±∞ÎÇò Ï∂îÎ°†ÎêòÍ≤å Ìï¥Ïïº Ìï©ÎãàÎã§.
export interface TreeNode<T> {
  id: string | number;
  label: string;
  icon?: LucideIcon;
  subInfo?: string[];
  children?: TreeNode<T>[]; // ÏûêÏãùÎèÑ Î∂ÄÎ™®ÏôÄ Í∞ôÏùÄ T ÌÉÄÏûÖ Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏßê
  data?: T; // ÏõêÎ≥∏ Îç∞Ïù¥ÌÑ∞ (Generic)
}

interface NavTreeProps<T> {
  title?: string;
  nodes: TreeNode<T>[]; // Ïó¨Í∏∞ÏÑú TÍ∞Ä Í≤∞Ï†ïÎê®
  openItems: Record<string, boolean>;
  onToggle: (id: string) => void;
  selectedId: string | number;
  onSelect: (node: TreeNode<T>) => void; // ÏÑ†ÌÉù Ïãú Î∞òÌôòÎêòÎäî ÎÖ∏ÎìúÎèÑ T ÌÉÄÏûÖÏùÑ Ïú†ÏßÄ
  className?: string;
}

// ‚ú® [ÌïµÏã¨ 2] Ïª¥Ìè¨ÎÑåÌä∏ ÏÑ†Ïñ∏ Ïãú Ï†úÎÑ§Î¶≠ <T>Îßå ÏÑ†Ïñ∏
// extends object: TÎäî ÏõêÏãúÌÉÄÏûÖ(Ïà´Ïûê, Î¨∏Ïûê)Ïù¥ ÏïÑÎãå Í∞ùÏ≤¥Ïó¨Ïïº Ìï®ÏùÑ Î™ÖÏãú (ÏïàÏ†ÑÏû•Ïπò)
export function NavTree<T extends object>({
  title = "NavTree",
  nodes,
  openItems,
  onToggle,
  selectedId,
  onSelect,
  className,
}: NavTreeProps<T>) {
  // Ïû¨Í∑Ä Î†åÎçîÎßÅ Ìï®Ïàò
  const renderNode = (node: TreeNode<T>, depth: number = 0) => {
    const hasChildren = node.children && node.children.length > 0;
    const isOpen = !!openItems[String(node.id)];
    const indentClass =
      depth === 0 ? "" : "ml-4 border-l border-slate-100 pl-2 mt-0.5";

    const IconComponent = node.icon;

    return (
      <div key={node.id} className={cn(indentClass, depth === 0 && "mb-1")}>
        {hasChildren ? (
          <Collapsible
            open={isOpen}
            onOpenChange={() => onToggle(String(node.id))}
          >
            <CollapsibleTrigger
              className={cn(
                "flex items-center w-full h-10 px-3 rounded-xl transition-all",
                depth === 0
                  ? "text-[13px] font-bold text-indigo-600 hover:bg-indigo-50"
                  : "text-[12.5px] font-bold text-slate-600 hover:bg-slate-50",
              )}
            >
              {isOpen ? (
                <ChevronDown className="h-3.5 w-3.5 mr-2" />
              ) : (
                <ChevronRight className="h-3.5 w-3.5 mr-2" />
              )}
              {IconComponent && (
                <IconComponent
                  className={cn(
                    "h-4 w-4 mr-2",
                    depth === 0 ? "text-indigo-600" : "text-indigo-400",
                  )}
                />
              )}
              <span className="flex-1 text-left truncate">{node.label}</span>
            </CollapsibleTrigger>

            <CollapsibleContent>
              {node.children!.map((child) => renderNode(child, depth + 1))}
            </CollapsibleContent>
          </Collapsible>
        ) : (
          <NavListItem
            icon={IconComponent!}
            title={node.label}
            subInfo={node.subInfo || []}
            isSelected={selectedId === node.id}
            onClick={() => onSelect(node)}
            className="h-10"
          />
        )}
      </div>
    );
  };

  return (
    <div className={cn("px-1 pb-4", className)}>
      <p className="text-[11px] font-bold text-slate-400 uppercase tracking-widest mb-3 px-2">
        {title}
      </p>
      {nodes.map((node) => renderNode(node))}
    </div>
  );
}
